import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			id: '',
			exist: false,
			waitLoad:false,
			detailList: []
		};
	},
	filters: {
		// 根据后台的coupons_status字段来判断样式,以下是模拟,
		coupons_status(val) {
			switch (val) {
				case 1:
					return 'cash_se1';
				case 2:
					return 'cash_se2';
				case 3:
					return 'cash_se3';
				default:
					return 'cash_se4';
			}
		}
	},
	methods: {
		leftClick() {




			uni.navigateBack({
				delta: 1
			});

		},
		list() {
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var {
				id
			} = this;
			var obj = {
				appid: appid,
				id: id,
				timeStamp: timeStamp
			};  
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var params = Object.assign({
					sign: sign
				},
				obj
			);
		
			rs.getRequests('couponsDetails', params, res => {
				this.waitLoad=true;
				if (res.data.code == 200) {
					this.detailList = res.data.data;
					let length = res.data.data.details.length;
					if (!length) {
						this.exist = true;
					}
				}
			});
		}
	},
	onLoad: function(options) {
		let {
			id
		} = options;
	
		this.id = id;
	
		this.list();
	}
};
